import { useState } from "react";
import React from 'react';

const CommentFooter = ({commentData, editing, setEditing, editComment}) => {

    const [text, setText] = useState(commentData.content);

    let handleEdit = () => {
        setEditing(false);
        editComment(text, commentData.id);
    }

    if(editing){
        return(
            <div className="edit">
                <textarea value={text} onChange={(e) => setText(e.target.value)}/>
                <button className="edit-btn" onClick={handleEdit}>UPDATE</button>
            </div>
        )
    }else {
        return(
            <p className="comment-footer">
                <span>{commentData.replyingTo !== undefined ? '@'+ commentData.replyingTo + ' ' : ''}</span>
                {commentData.content}
            </p>
        )
    }
}

export default CommentFooter;