import Plus from '../images/icon-plus.svg';
import Minus from '../images/icon-minus.svg';
import React from 'react';

const CommentVotes = ({vote, setVote, updateScore, id, type, voted}) => {

    //To be reviewed and linked to currentUser var. (can do on backend) 
    let upVote = () => {
        //No vote added 
        if(!voted.plus && !voted.minus){
            let n = vote + 1;
            setVote(n);
            updateScore(n, id, type);
        }else if(voted.plus && !voted.minus){
            //Voted on Plus and another click 
            let n = vote - 1;
            setVote(n);
            updateScore(n, id, type);
        }else if(!voted.plus && voted.minus){
            //Voted on Minus and click on Plus
            let n = vote + 2;
            setVote(n);
            updateScore(n, id, type);
        }
    }

    let downVote = () => {
        if(!voted.plus && !voted.minus){
            let n = vote - 1;
            setVote(n);
            updateScore(n, id, type);
        }else if(!voted.plus && voted.minus){
            let n = vote + 1;
            setVote(n);
            updateScore(n, id, type);
        }else if(voted.plus && !voted.minus){
            let n = vote - 2;
            setVote(n);
            updateScore(n, id, type);
        }
    }

    return(
        <div className="votes">
            <button className={voted.plus ? "clicked" : ""} type="button" onClick={upVote}><img src={Plus} alt='Plus'></img></button>
            <p>{vote}</p>
            <button className={voted.minus ? "clicked minus" : "minus"} type="button" onClick={downVote}><img src={Minus} alt='Minus'></img></button>
        </div>
    )
}

export default CommentVotes;