import { useState } from "react";
import React from 'react';

import CommentVotes from "./CommentVotes";
import CommentHeader from "./CommentHeader";
import CommentFooter from "./CommentFooter";
import AddComment from "./AddComment";

const Reply = ({commentData, id, updateScore, addComment, deleteComment,  editComment, enter}) => {

    const [vote, setVote] = useState(commentData.score);
    const [replying, setReplying] = useState(false);
    const [editing, setEditing] = useState(false);
    const [deleteModal, setDeleteModal] = useState(false);

    let handleVote = (n, id, type) => {
        updateScore(n, id, type)
    }

    let handleReplying = () => {
        setReplying(!replying);
    }

    let handleReply = () => {
        setReplying(!replying);
    }

    let handleModalDelete = () => {
        setDeleteModal(true)
    }

    let handleBack = () => {
        setDeleteModal(false);
    }

    let handleDelete = () =>  {
        deleteComment(id, 'reply');
    }
    
    if(replying === false){
        return(
            <div className={enter ? 'comment-container comment-charge' : 'comment-container'}>
                <CommentVotes
                    vote={vote}
                    setVote={setVote}
                    updateScore={handleVote}
                    id={id}
                    type={'reply'}
                    voted={commentData.voted}
                ></CommentVotes>
                <div className="content">
                    <CommentHeader
                        replying={replying}
                        setReplying={handleReplying}
                        commentData={commentData}
                        deleteComment={handleModalDelete}
                        editing={editing}
                        setEditing={setEditing}
                    ></CommentHeader>
                    {deleteModal && 
                            <div id='cache' onClick={handleBack}>
                                <div className="delete-modal">
                                    <h2>Delete comment</h2>
                                    <p>Are you sure you want to delete this comment ? This will remove the comment and can't be undone.</p>
                                    <div>
                                        <button onClick={handleBack}>NO, CANCEL</button>
                                        <button className="delete-btn" onClick={handleDelete}>YES, DELETE</button>
                                    </div>
                                </div>
                            </div>
                    }     
                    <CommentFooter
                        commentData={commentData}
                        editing={editing}
                        setEditing={setEditing}
                        editComment={editComment}
                    ></CommentFooter>
                </div>
            </div>
        )
    }else {
        return(
            <div className="comment-w-replies">
                <div className={enter ? 'comment-container comment-charge' : 'comment-container'}>
                    <CommentVotes
                        vote={vote}
                        setVote={setVote}
                        updateScore={handleVote}
                        id={id}
                        type={'reply'}
                        voted={commentData.voted}
                    ></CommentVotes>
                    <div className="content">
                        <CommentHeader
                            replying={replying}
                            setReplying={handleReplying}
                            commentData={commentData}
                            deleteComment={handleModalDelete}
                            editing={editing}
                            setEditing={setEditing}
                        ></CommentHeader>
                        {deleteModal && 
                            <div id='cache' onClick={handleBack}>
                                <div className="delete-modal">
                                    <h2>Delete comment</h2>
                                    <p>Are you sure you want to delete this comment ? This will remove the comment and can't be undone.</p>
                                    <div>
                                        <button onClick={handleBack}>NO, CANCEL</button>
                                        <button className="delete-btn" onClick={handleDelete}>YES, DELETE</button>
                                    </div>
                                </div>
                            </div>
                        }
                        <CommentFooter
                            commentData={commentData}
                            editing={editing}
                            setEditing={setEditing}
                            editComment={editComment}
                        ></CommentFooter>
                    </div>
                </div>
                <AddComment 
                    addComment={addComment}
                    replyingTo={commentData.user.username}
                    setReplying={handleReply}
                />
            </div>
        )
    }
}

export default Reply;