const goodTime = (time) => {
    const createdAtData = new Date(time);
    const timestamp = new Date() - createdAtData;
    var actual = timestamp/1000;

    if( actual >= 60 && actual < 3600){
        actual = actual/60;
        actual = Math.round(actual);
        if(actual > 1){
            return actual + " mins ago";
        }else{
            return actual + " min ago";
        }
    }else if(actual >= 3600 && actual < 86400){
        actual = actual / 3600;
        actual = Math.round(actual);
        if(actual > 1){
            return actual + " hours ago";
        }else{
            return actual + " hour ago";
        }
    }else if(actual >= 86400 && actual < 604800){
        actual = actual / 86400;
        actual = Math.round(actual);
        if(actual > 1){
            return actual + " days ago";
        }else{
            return actual + " day ago";
        }
    }else if(actual >= 604800 && actual < 2592000){
        actual = actual / 604800;
        actual = Math.round(actual);
        if(actual > 1){
            return actual + " weeks ago";
        }else{
            return actual + " week ago";
        }
    }else if(actual >= 2592000){
        actual = actual / 2592000;
        actual = Math.round(actual);
        if(actual > 1){
            return actual + " months ago";
        }else{
            return actual + " month ago";
        }
    }
    actual = Math.round(actual);
    if(actual > 1){
        return actual + " secondes ago";
    }else{
        return actual + " seconde ago";
    }
}

export {goodTime};