import React, { useState, useEffect } from "react";
import data from './data.json';
import Comment from './Components/Comment';
import AddComment from './Components/AddComment';


const App = () => {

    const [comments, updateComments] = useState(() => {
        //getting storage value if exist
        const saved = localStorage.getItem('comments');
        const initial = JSON.parse(saved);
        return initial || data.comments;
    });

    //set storage value with border effects
    useEffect(() => {
        localStorage.setItem('comments', JSON.stringify(comments));
    }, [comments]);

    //Add comment function
    let addComment = (newComment) => {
        let updatedComments = [...comments];

        if(newComment.user === undefined){
            newComment.user = data.currentUser;
        }

        if(newComment.replyingTo === undefined){
            updatedComments.push(newComment);
        }else{
            updatedComments.forEach((comment) => {
                if(comment.user.username === newComment.replyingTo){
                    comment.replies.push(newComment);
                }else{
                    comment.replies.forEach((reply) => {
                        if(reply.user.username === newComment.replyingTo){
                            comment.replies.push(newComment);
                        }
                    })
                }
            })
        };
        updateComments(updatedComments);
    };

    //Update Score and sort comment
    let updateScore = (n, id, type) => {
        let updatedComments = [...comments];

        //Update score of selected comment
        updatedComments.forEach((comment) => {
            if(type === 'comment'){
                if(comment.id === id){
                    if(n - comment.score === -1 && comment.voted.plus === true){
                        comment.voted.plus = false;
                    }else if(n - comment.score === 1 && comment.voted.minus === true){
                        comment.voted.minus = false;
                    }else if(n - comment.score === 1 && (comment.voted.minus === false && comment.voted.plus === false)){
                        comment.voted.plus = true;
                        comment.voted.minus = false;
                    }else if(n - comment.score === -1 && (comment.voted.minus === false && comment.voted.plus === false)){
                        comment.voted.plus = false;
                        comment.voted.minus = true;
                    }else if(n - comment.score === 2){
                        comment.voted.plus = true;
                        comment.voted.minus = false;
                    }else if(n - comment.score === -2){
                        comment.voted.plus = false;
                        comment.voted.minus = true;
                    }
                    comment.score = n;
                }
            }else if(type === 'reply'){
                comment.replies.forEach((reply) => {
                    if(reply.id === id){
                        if(n - reply.score === -1 && reply.voted.plus === true){
                            reply.voted.plus = false;
                        }else if(n - reply.score === 1 && reply.voted.minus === true){
                            reply.voted.minus = false;
                        }else if(n - reply.score === 1 && (reply.voted.minus === false && reply.voted.plus === false)){
                            reply.voted.plus = true;
                            reply.voted.minus = false;
                        }else if(n - reply.score === -1 && (reply.voted.minus === false && reply.voted.plus === false)){
                            reply.voted.plus = false;
                            reply.voted.minus = true;
                        }else if(n - reply.score === 2){
                            reply.voted.plus = true;
                            reply.voted.minus = false;
                        }else if(n - reply.score === -2){
                            reply.voted.plus = false;
                            reply.voted.minus = true;
                        }
                        reply.score = n;
                    }
                });
            };
        });

        //Sort updatedComments and replies
        updatedComments.sort((a, b) => {
            return b.score - a.score;
        });

        updatedComments.forEach((comment) => {
            comment.replies.sort((a, b) => {
                return b.score - a.score;
            })
        });

        
        updateComments(updatedComments);
    }


    //Delete comment function 
    let deleteComment = (id, type) => {
        let currentComments = [...comments];
        let updatedComments = [];
        let updatedReply = [];

        //Looking for 'comment' and delete 
        if(type === 'comment'){
            currentComments.forEach((comment) => {
                if(comment.id !== id){
                    updatedComments.push(comment)
                }
            });
        };

        //Looking for 'reply' and delete
        if(type === 'reply'){
            updatedComments = currentComments.filter((comment) => {
                comment.replies.forEach((reply) => {
                    if(reply.id !== id){
                        updatedReply.push(reply);
                    }
                });
                comment.replies = updatedReply;
                updatedReply = [];
                return comment;
            })
        };

        updateComments(updatedComments);
    }

    //Edit comment
    let editComment = (text, id) => {
        let updatedComments = [...comments];

        //Replace arg. text with content 
        updatedComments.forEach((comment) => {
            comment.replies.forEach((reply) => {
                if(reply.id === id){
                    reply.content = text;
                };
            });
            if(comment.id === id){
                comment.content = text;
            };
        });

        updateComments(updatedComments);
    }

    return (
        <main className="app">
            <h1 className="sr-only">Challenge FrontendMentor Comment App</h1>
            <div className="overlay">
                <div className="overlay__row"></div>
                <div className="overlay__row"></div>
            </div>
            {comments.map((comment) => (
                
                <Comment
                    key={comment.id}
                    commentData={comment}
                    updateScore={updateScore}
                    addComment={addComment}
                    deleteComment={deleteComment}
                    editComment={editComment}
                />
            ))}
            <AddComment
                addComment={addComment}
            />
        </main>
    )
    
}

export default App;