import CommentButtons from './CommentButtons';
import { goodTime } from '../functions/time';
import React from 'react';

const CommentHeader = ({commentData, replying, setReplying, deleteComment, editing, setEditing}) => {

    
    if(commentData.user.username === 'juliusomo'){
        return(
            <div className='comment-header'>
                <div className='comment-info'>
                    <div className={commentData.user.username}></div>
                    <h4>{commentData.user.username}</h4>
                    <span>you</span>
                    <p>{goodTime(commentData.createdAt)}</p>
                </div>
                <CommentButtons
                    user={commentData.user.username}
                    replying={replying}
                    setReplying={setReplying}
                    deleteComment={deleteComment}
                    editing={editing}
                    setEditing={setEditing}
                ></CommentButtons>
            </div>
        )
    }else{
        return(
            <div className='comment-header'>
                <div className='comment-info'>
                    <div className={commentData.user.username}></div>
                    <h4>{commentData.user.username}</h4>
                    <p>{goodTime(commentData.createdAt)}</p>
                </div>
                <CommentButtons
                    user={commentData.user.username}
                    replying={replying}
                    setReplying={setReplying}
                ></CommentButtons>
            </div>
        )
    }
}

export default CommentHeader;