import React from 'react';
import Delete from '../images/icon-delete.svg';
import Reply from '../images/icon-reply.svg';
import Edit from '../images/icon-edit.svg';

const CommentButtons = ({user, replying, setReplying, deleteComment, editing, setEditing}) => {

    let handleReplying = () => {
        setReplying();
    }

    let handleDelete = () => {
        deleteComment();
    }

    let handleEdit = () => {
        setEditing(!editing);
    }

    if(user ==='juliusomo'){
        return(
            <div className='comment-buttons'>
                <button className='delete'  onClick={handleDelete}>
                    <img src={Delete} alt="Delete icon"></img>
                    Delete
                </button>
                <button className={ editing ? 'clicked' : ''} onClick={handleEdit}>
                    <img src={Edit} alt="Edit icon"></img>
                    Edit
                </button>
            </div>
        )
    }else{
    return(
        <div className='comment-buttons'>
            <button className={replying ? 'clicked' : ''} onClick={handleReplying}>
                <img src={Reply} className='reply' alt="Reply icon"></img>
                Reply
            </button>
        </div>
    )
    }
}

export default CommentButtons;