import Reply from "./Reply";
import React from "react";

const ReplyContainer = ({commentData, updateScore, addComment, deleteComment, editComment, enter}) => {

    let handleVote = (n, id, type) => {
        updateScore(n, id, type)
    }

    return(
        <div className={ enter ? "comment-charge reply-container" : 'reply-container'}>
            {commentData.map((reply) => (
                <Reply
                    key={reply.id}
                    commentData={reply}
                    id={reply.id}
                    updateScore={handleVote}
                    addComment={addComment}
                    deleteComment={deleteComment}
                    editComment={editComment}
                    enter={enter}
                />
            ))}
        </div>
    )
}

export default ReplyContainer;