//General import
import { useState, useEffect } from "react";
import React from 'react';

//Import Components
import CommentHeader from "./CommentHeader";
import CommentVotes from "./CommentVotes";
import CommentFooter from "./CommentFooter";
import ReplyContainer from "./ReplyContainer";
import AddComment from "./AddComment";

const Comment = ({commentData, updateScore, addComment, deleteComment, editComment}) => {
    const [vote, setVote] = useState(commentData.score);
    const [replying, setReplying] = useState(false);
    const [editing, setEditing] = useState(false);
    const [deleteModal, setDeleteModal] = useState(false);
    const [enter, setEnter] = useState(true);

    useEffect(() =>{
        setTimeout(handleEnter, 800);
    })

    let handleEnter = () => {
        setEnter(false);
    }

    let handleVote = (n, id, type) => {
        updateScore(n, id, type)
    }

    let handleReplying = () => {
        setReplying(!replying);
    }

    let handleModalDelete = () => {
        setDeleteModal(true)
    }

    let handleBack = () => {
        setDeleteModal(false);
    }

    let handleDelete = () => {
        deleteComment(commentData.id, 'comment');
        setDeleteModal(undefined);
    }

    let handleEdit = (text, id) => {
        setEditing(false);
        editComment(text, id);
    }

    if(commentData.replies[0] === undefined && replying === false){
        return(
            <div className={enter ? 'comment-container comment-charge' : 'comment-container'}>
                <CommentVotes
                    vote={vote}
                    setVote={setVote}
                    updateScore={handleVote}
                    id={commentData.id}
                    type={'comment'}
                    voted={commentData.voted}
                ></CommentVotes>
                <div className="content">
                    <CommentHeader
                        replying={replying}
                        setReplying={handleReplying}
                        commentData={commentData}
                        deleteComment={handleModalDelete}
                        editing={editing}
                        setEditing={setEditing}
                    ></CommentHeader>
                    {deleteModal && 
                    <div id='cache' onClick={handleBack}>
                        <div className="delete-modal">
                            <h2>Delete comment</h2>
                            <p>Are you sure you want to delete this comment ? This will remove the comment and can't be undone.</p>
                            <div>
                                <button onClick={handleBack}>NO, CANCEL</button>
                                <button className="delete-btn" onClick={handleDelete}>YES, DELETE</button>
                            </div>
                        </div>
                    </div>
                    }
                    <CommentFooter
                        commentData={commentData}
                        editing={editing}
                        setEditing={setEditing}
                        editComment={handleEdit}
                    ></CommentFooter>
                </div>
            </div>
        )
    }else if(commentData.replies[0] === undefined && replying === true){
        return(
            <div className="comment-w-replies">
                <div className={enter ? 'comment-container comment-charge' : 'comment-container'}>
                    <CommentVotes
                        vote={vote}
                        setVote={setVote}
                        updateScore={handleVote}
                        id={commentData.id}
                        type={'comment'}
                        voted={commentData.voted}
                    ></CommentVotes>
                    <div className="content">
                        <CommentHeader
                            replying={replying}
                            setReplying={handleReplying}
                            commentData={commentData}
                            deleteComment={handleModalDelete}
                            editing={editing}
                            setEditing={setEditing}
                        ></CommentHeader>
                        {deleteModal && 
                            <div id='cache' onClick={handleBack}>
                                <div className="delete-modal">
                                    <h2>Delete comment</h2>
                                    <p>Are you sure you want to delete this comment ? This will remove the comment and can't be undone.</p>
                                    <div>
                                        <button onClick={handleBack}>NO, CANCEL</button>
                                        <button className="delete-btn" onClick={handleDelete}>YES, DELETE</button>
                                    </div>
                                </div>
                            </div>
                        }
                        <CommentFooter
                            commentData={commentData}
                            editing={editing}
                            setEditing={setEditing}
                            editComment={handleEdit}
                        ></CommentFooter>
                    </div>
                </div>
                <AddComment 
                    addComment={addComment}
                    replyingTo={commentData.user.username}
                    setReplying={handleReplying}
                />
            </div>
        )
    }else if(commentData.replies[0] !== undefined && replying === false){
        return(
        <div className="comment-w-replies">
            <div className={enter ? 'comment-container comment-charge' : 'comment-container'}>
                <CommentVotes
                    vote={vote}
                    setVote={setVote}
                    updateScore={handleVote}
                    id={commentData.id}
                    type={'comment'}
                    voted={commentData.voted}
                ></CommentVotes>
                <div className="content">
                    <CommentHeader
                        replying={replying}
                        setReplying={handleReplying}
                        commentData={commentData}
                        deleteComment={handleModalDelete}
                        editing={editing}
                        setEditing={setEditing}
                    ></CommentHeader>
                    {deleteModal && 
                        <div id='cache' onClick={handleBack}>
                            <div className="delete-modal">
                                <h2>Delete comment</h2>
                                <p>Are you sure you want to delete this comment ? This will remove the comment and can't be undone.</p>
                                <div>
                                    <button onClick={handleBack}>NO, CANCEL</button>
                                    <button className="delete-btn" onClick={handleDelete}>YES, DELETE</button>
                                </div>
                            </div>
                        </div>
                    }                    
                    <CommentFooter
                        commentData={commentData}
                        editing={editing}
                        setEditing={setEditing}
                        editComment={handleEdit}
                    ></CommentFooter>
                </div>
            </div>
            <ReplyContainer
                    commentData={commentData.replies}
                    updateScore={handleVote}
                    addComment={addComment}
                    deleteComment={deleteComment}
                    editComment={handleEdit}
                    enter={enter}
                />
        </div>
        )
    }else {
        return(
            <div className="comment-w-replies">
                <div className={enter ? 'comment-container comment-charge' : 'comment-container'}>
                    <CommentVotes
                        vote={vote}
                        setVote={setVote}
                        updateScore={handleVote}
                        id={commentData.id}
                        type={'comment'}
                        voted={commentData.voted}
                    ></CommentVotes>
                    <div className="content">
                        <CommentHeader
                            replying={replying}
                            setReplying={handleReplying}
                            commentData={commentData}
                            deleteComment={handleModalDelete}
                            editing={editing}
                            setEditing={setEditing}
                        ></CommentHeader>
                        {deleteModal && 
                            <div id='cache' onClick={handleBack}>
                                <div className="delete-modal">
                                    <h2>Delete comment</h2>
                                    <p>Are you sure you want to delete this comment ? This will remove the comment and can't be undone.</p>
                                    <div>
                                        <button onClick={handleBack}>NO, CANCEL</button>
                                        <button className="delete-btn" onClick={handleDelete}>YES, DELETE</button>
                                    </div>
                                </div>
                            </div>
                        }     
                        <CommentFooter
                            commentData={commentData}
                            editing={editing}
                            setEditing={setEditing}
                            editComment={handleEdit}
                        ></CommentFooter>
                    </div>
                </div>
                <AddComment 
                    addComment={addComment}
                    replyingTo={commentData.user.username}
                    setReplying={handleReplying}
                />
                <ReplyContainer
                        commentData={commentData.replies}
                        updateScore={handleVote}
                        addComment={addComment}
                        deleteComment={deleteComment}
                        editComment={handleEdit}
                        enter={enter}
                />
            </div>
        )
    }
}

export default Comment;