import { useState } from "react";
import React from 'react';
import data from '../data.json';

const AddComment = ({addComment, replyingTo, setReplying}) => {
    const [text, updateText] = useState(replyingTo ? '@' + replyingTo + ' ' : "");
    const currentUser = data.currentUser;

    const handleClick = () => {
        if(text === '' || text === ' ') return;

        if(text === '@' + replyingTo + ' ' || text === '@' + replyingTo + '  '){
            setReplying();
            return;
        };

        const newComment = {
            id: Math.random(),
            createdAt: new Date().toString(),
            content: text.replace(replyingTo ? '@' + replyingTo + ' ' : "", ''),
            score: 0,
            voted:{"plus": false, "minus" : false},
            user: currentUser,
            replyingTo: replyingTo,
            replies: []
        };

        addComment(newComment);
        updateText("");
        if(setReplying !== undefined){
            setReplying();
        }
    };

    return(
        <div className="add-comment comment-container">
            <div className={currentUser.username}></div>
            <textarea name="Add-Comment" placeholder="Add a comment..." value={text}
                onChange={(e) => updateText(e.target.value)}
            ></textarea>
            {replyingTo ? (
                <button className="reply-btn" type="button" onClick={handleClick}>REPLY</button>
            ) : (
                <button type="button" onClick={handleClick}>SEND</button>
            )}
        </div>
    )
}

export default AddComment;